<template lang='pug'>
div
  v-card

</template>

<script>
export default {
  name: 'g-vtuber-card',
  props: {
  },
  data() {
    return {
      part: 'snippet,contentDetails,statistics',
      id: 'UC_a1ZYZ8ZTXpjg9xUY9sj8w',
      key: 'AIzaSyCYwvjFZE1jgRFbDIAsV-CCMPVRteoxWFA',
    }
  },
  created() {
    this.$http.get(`https://www.googleapis.com/youtube/v3/channels?part=${this.part}&id=${this.id}&key=${this.key}`).then((response) => {
      this.$log.debug('youtube list', response)
      /* 成功拿到資料，然後... */
      }, (response) => {
        this.$log.debug('request fail', JSON.parse(response.bodyText))
      // /* 失敗，發生錯誤，然後...*/
      });
  },
  methods: {
  }
}
</script>

